<template>
    <div class="mine">
      <div class="backgroundArea" :style="{ backgroundImage: 'url('+ backgroundImg +')' }">
        <div class="redDot" v-if="myInfo.userType !== 'M'"></div>
        <span class="accountState" v-if="myInfo.userType !== 'M'" @click="validate">{{ myInfo.auditStatus === '0' ? '已认证' : (myInfo.auditStatus === '1' ? '未认证' : '认证中')}}</span>
        <div class="header">
          <div class="headTop">
            <div class="userHead">
              <img :src="myInfo.userPhoto === '' ? headNormal : myInfo.userPhoto">
              <div class="usertype">{{ userTip }}</div>
            </div>
            <div class="userInfo">
              <span class="userName">{{ myInfo.nickName }}
                <span class="write" @click="changeFile">
                  <img src="../assets/img/write.png">
                </span>
              </span>
              <span class="sign">{{ userSign }}</span>
            </div>
            <div class="code">
              <img src="../assets/img/code.png">
            </div>
          </div>
          <div class="headBottom">
            <div class="trends">
              <span class="trend">
                <span class="trendNum" @click="showDynamic">{{ myInfo.moments }}</span>
                <span class="trendTitle">动态</span>
              </span>
              <span class="trend">
                <span class="trendNum" @click="showConcern">{{ myInfo.interest }}</span>
                <span class="trendTitle">关注</span>
              </span>
              <span class="trend">
                <span class="trendNum" @click="showFans">{{ myInfo.fans }}</span>
                <span class="trendTitle">粉丝</span>
              </span>
            </div>
            <div class="signIn">
              <button @click="signIn">签到</button>
            </div>
          </div>
        </div>
      </div>
        <div class="perfectInfo">您的资料还未完善，点此完善资料获得奖励</div>
        <ul class="details">
          <li v-for="(detail, index) in details" :key="index" class="detail" @click="myProfile(index)">
            <span class="detailImg">
              <img :src="detail.image">
            </span>
            <span class="detailTitle">{{ detail.title }}</span>
          </li>
        </ul>
      <BottomNav></BottomNav>
    </div>
</template>

<script>
import { getUserInfo } from '../api/getdata'
import store from '../vuex/store'
import { Toast } from 'mint-ui'
import BottomNav from '../components/BottomNav'
export default {
  name: 'Mine',
  components: {
    BottomNav
  },
  data () {
    return {
      validateStatus: '',
      backgroundImg: require('../assets/img/mineBackground.png'),
      headNormal: require('../assets/img/headNormal.png'),
      userId: '',
      userType: '',
      myInfo: {},
      detailsM: [{
        image: require('../assets/img/mywallet.png'), title: '我的钱包', linkTo: 'myWallet'
      }, {
        image: require('../assets/img/mytraining.png'), title: '我的修炼', linkTo: 'setting'
      }, {
        image: require('../assets/img/mycollection.png'), title: '我的收藏', linkTo: 'myCollection'
      }, {
        image: require('../assets/img/mycompetition.png'), title: '我的赛事', linkTo: 'test'
      }, {
        image: require('../assets/img/myteam.png'), title: '我的哼哈团', linkTo: 'myRegiment'
      }, {
        image: require('../assets/img/mysetting.png'), title: '设置', linkTo: 'setting'
      }],
      detailsT: [{
        image: require('../assets/img/mywallet.png'), title: '我的钱包', linkTo: 'myWallet'
      }, {
        image: require('../assets/img/mylesson.png'), title: '课程管理', linkTo: 'myLesson'
      }, {
        image: require('../assets/img/myvariety.png'), title: '套餐管理', linkTo: 'lessonPrice'
      }, {
        image: require('../assets/img/myvideo.png'), title: '教学视频', linkTo: 'myVideo'
      }, {
        image: require('../assets/img/mycollection.png'), title: '我的收藏', linkTo: 'myCollection'
      }, {
        image: require('../assets/img/myteam.png'), title: '我的哼哈团', linkTo: 'myRegiment'
      }, {
        image: require('../assets/img/mysetting.png'), title: '设置', linkTo: 'setting'
      }],
      detailsC: [{
        image: require('../assets/img/mywallet.png'), title: '我的钱包', linkTo: 'myWallet'
      }, {
        image: require('../assets/img/coachManage.png'), title: '教练管理', linkTo: 'coachManage'
      }, {
        image: require('../assets/img/mylesson.png'), title: '课程管理', linkTo: 'myLesson'
      }, {
        image: require('../assets/img/myvariety.png'), title: '套餐管理', linkTo: 'test'
      }, {
        image: require('../assets/img/myvideo.png'), title: '教学视频', linkTo: 'myVideo'
      }, {
        image: require('../assets/img/mycollection.png'), title: '我的收藏', linkTo: 'myCollection'
      }, {
        image: require('../assets/img/mysetting.png'), title: '设置', linkTo: 'setting'
      }]
    }
  },
  created () {
    this.userType = localStorage.getItem('userType')
    this.userId = localStorage.getItem('userId')
    // this.userType = getCookie('userType')
    // this.userId = getCookie('userId')
    // console.log(this.userType)
    this.getMyInfo()
    // console.log(localStorage.getItem('account') + '/' + localStorage.getItem('password'))
  },
  computed: {
    userTip: function () {
      if (this.userType === 'M') {
        return '学'
      } if (this.userType === 'T') {
        return '教'
      } else {
        return '馆'
      }
    },
    details: function () {
      if (this.userType === 'M') {
        return this.detailsM
      } else if (this.userType === 'T') {
        return this.detailsT
      } else {
        return this.detailsC
      }
    },
    userSign: function () {
      if (this.myInfo.userSignature === null) {
        return '您还没有设置签名！'
      } else {
        return this.myInfo.userSignature
      }
    }
  },
  methods: {
    getMyInfo () {
      let data = getUserInfo({fUserId: this.userId})
      data.then(res => {
        console.log(res)
        this.myInfo = res.data
        this.validateStatus = res.data.auditStatus
      })
    },
    myProfile (index) {
      if (this.details[index].linkTo === 'test') {
        Toast({
          message: '该功能尚未开放',
          duration: 1500
        })
      } else if (this.details[index].linkTo === 'myRegiment') {
        if (this.myInfo.userId === 185) {
          this.$router.push(`/home/mine/${this.details[index].linkTo}`)
        } else {
          Toast({
            message: '该功能尚未开放',
            duration: 1500
          })
        }
      } else {
        store.commit('getUserId', this.myInfo.userId)
        store.commit('getUserClick', 'self')
        this.$router.push(`/home/mine/${this.details[index].linkTo}`)
      }
    },
    changeFile () {
      this.$router.push('/home/mine/myprofile')
    },
    showFans () {
      // console.log('查询粉丝列表')
      store.commit('getUserId', this.myInfo.userId)
      this.$router.push('/home/community/fans')
    },
    showConcern () {
      // console.log('查询关注列表')
      store.commit('getUserId', this.myInfo.userId)
      this.$router.push('/home/community/concerned')
    },
    showDynamic () {
      // console.log('显示动态列表')
      store.commit('getUserId', this.myInfo.userId)
      // store.commit('getUserType', this.myInfo.userType)
      this.$router.push('/home/community/dynamic')
    },
    signIn () {
      this.$router.push('/home/mine/signIn')
    },
    validate () {
      if (this.validateStatus === '1') {
        if (this.userType === 'T') {
          this.$router.push('/home/enroll/coachStep1')
        } else {
          this.$router.push('/home/enroll/gymAuthentication')
        }
      }
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.426667rem;
  }

  .backgroundArea {
    width: 100%;
    height: 4.74667rem;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center;
    position: fixed;
    top: 0;
    left: 0;
  }

  .accountState {
    position: absolute;
    right: 0.4rem;
    top: 0.4rem;
  }

  .redDot {
    width: 0.16rem;
    height: 0.16rem;
    border-radius: 50%;
    position: absolute;
    background: red;
    right: 1.7rem;
    top: 0.36rem;
  }

  .header {
    width: calc(100% - 1.3333rem);
    margin: 0 auto;
    border-radius: 0.15rem;
    margin-top: 1.5rem;
    background: #fff;
    padding: 0.2rem;
    z-index: 10;
    box-shadow: 0 0 0 0.06rem #f5f5f5;
  }

  .headTop {
    border-bottom: 1px solid #f5f5f5;
    padding-bottom: 0.21333rem;
    display: flex;
    justify-content: space-between;
  }

  .userHead {
    width: 1.86667rem;
    height: 1.86667rem;
    border-radius: 50%;
    flex-shrink: 0;
    position: relative;
  }

  .usertype {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: -0.1rem;
    bottom: -0.1rem;
    color: #fffcfc;
    width: 0.6rem;
    height: 0.6rem;
    border-radius: 50%;
    border: 2px solid #fffcfc;
    background: #ffd200;
    font-size: 0.373333rem;
  }

  .userInfo {
    width: 6.26667rem;
    margin-top: 0.4rem;
    padding-left: 0.45333rem;
  }

  .userName,
  .sign {
    display: inline-block;
    width: 100%;
    text-align: start;
    color: #333333;
  }

  .sign {
    font-size: 0.373333rem;
    color: #666666;
    line-height: 0.4rem;
    margin-top: 0.1rem;
  }

  .code {
    width: 0.53333rem;
    height: 0.53333rem;
    margin-top: 0.4rem;
  }

  .write {
    display: inline-block;
    width: 0.5rem;
    height: 0.5rem;
    margin-left: 0.24rem;
  }

  img {
    width: 100%;
    height: 100%;
  }

  .userHead img {
    border-radius: 0.93333rem;
  }

  .headBottom {
    display: flex;
  }

  .trends {
    width: 50%;
    display: flex;
    justify-content: space-between;
  }

  .trend span {
    display: block;
    color: #666666;
  }

  .signIn {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }

  .signIn button {
    width: 1.86667rem;
    height: 0.96rem;
    border-radius: 0.48rem;
    border: none;
    outline: none;
    background: linear-gradient(to right bottom, #ffd200, #ff8309);
    color: #fffcfc;
  }

  .perfectInfo {
    width: 100%;
    height: 0.9rem;
    background: #ffe6c8;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.373333rem;
    color: #666666;
    margin-top: 5.2rem;
  }

  .details {
    margin-top: 0.45333rem;
    display: flex;
    flex-wrap: wrap;
  }

  .detail {
    display: flex;
    width: 33%;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin-bottom: 0.826667rem;
    list-style: none;
  }

  .detailImg,
  .detailTitle {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 1.46667rem;
    font-size: 0.373333rem;
  }

  .detailTitle {
    width: 100%;
    margin-top: 0.2rem;
  }
</style>



